<template>
  <div class="pop">
    <v-dialog v-model="dialog" persistent>
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="enterAdd"
          >mdi-plus-circle</v-icon
        >
      </template>

      <v-card>
        <v-card-title :class="titleClass" primary-title>
          {{ title }}
          <v-spacer></v-spacer>
          <v-icon @click="checkClose()">mdi-close</v-icon>
        </v-card-title>

        <v-card-text>
          <v-form ref="form">
            <v-row>
              <v-col cols="12" md="6">
                <v-text-field
                  label="活動名稱"
                  v-model="list.name"
                  :rules="emptyRules"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="3">
                <v-text-field
                  label="日期"
                  type="date"
                  v-model="list.date"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="3">
                <v-text-field label="時間" v-model="list.time"></v-text-field>
              </v-col>
              <v-col cols="12" md="6">
                <v-text-field label="目標" v-model="list.target"></v-text-field>
              </v-col>
              <v-col cols="12" md="6">
                <v-text-field
                  label="地點"
                  v-model="list.location"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="8">
                <v-autocomplete
                  label="參加成員"
                  prepend-icon="mdi-account-multiple"
                  v-model="list.memberArray"
                  :items="uitems"
                  :rules="emptyRules"
                  reauired
                  multiple
                  return-object
                  chips
                ></v-autocomplete>
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field
                  label="參加人數"
                  readonly
                  outlined
                  :value="userCounts"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="6">
                <v-text-field
                  label="器具設備"
                  v-model="list.device"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="3">
                <v-text-field
                  label="帶領者"
                  v-model="list.leader"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="3">
                <v-text-field
                  label="協助者"
                  v-model="list.helper"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-card>
                  <v-card-title>
                    <h3>活動時間表</h3>
                    <v-icon color="red" @click="activityAdd"
                      >mdi-plus-circle</v-icon
                    >
                  </v-card-title>
                  <v-card-text>
                    <v-row
                      v-for="(item, index) in list.scheduleArray"
                      :key="index"
                    >
                      <v-col cols="12" md="4">
                        <v-text-field
                          label="活動時間"
                          hide-details
                          v-model="item.time"
                        ></v-text-field>
                      </v-col>
                      <v-col cols="12" md="8" class="d-flex">
                        <v-text-field
                          label="活動內容"
                          hide-details
                          v-model="item.content"
                        ></v-text-field>
                        <v-icon color="red" @click="activityDel(index)"
                          >mdi-close</v-icon
                        >
                      </v-col>
                    </v-row>
                  </v-card-text>
                </v-card>
              </v-col>
              <v-col cols="12">
                <v-card>
                  <v-card-title>
                    <h3>活動照片</h3>
                    <v-icon color="red" @click="$refs.fileInput.click()"
                      >mdi-plus-circle</v-icon
                    >
                    <input
                      type="file"
                      multiple
                      ref="fileInput"
                      @change="onFileSelected"
                      style="display: none"
                    />
                  </v-card-title>
                  <v-card-text>
                    <v-row>
                      <v-col
                        cols="4"
                        v-for="(item, index) in list.picItems"
                        :key="index"
                      >
                        <v-img :src="base_url + '/upload/activity/' + item">
                          <v-btn
                            icon
                            small
                            class="float-right white ma-1"
                            @click.stop="delPic(item, index)"
                          >
                            <v-icon color="red">mdi-close</v-icon>
                          </v-btn>
                        </v-img>
                      </v-col>
                    </v-row>
                  </v-card-text>
                </v-card>
              </v-col>
              <v-col cols="12" md="6">
                <v-textarea
                  label="活動說明"
                  v-model="list.explanation"
                ></v-textarea>
              </v-col>
              <v-col cols="12" md="6">
                <v-textarea
                  label="活動檢討與建議"
                  v-model="list.suggest"
                ></v-textarea>
              </v-col>
            </v-row>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="error" @click="addOK" v-if="process == 'add'"
            >確認新增</v-btn
          >
          <v-btn class="success" @click="editOK" v-if="process == 'edit'"
            >確認修改</v-btn
          >
        </v-card-actions>

        <!-- <pre>
          list: {{ list.picItems }}

        </pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialog: false,
      list: {
        memberArray: [],
        scheduleArray: [{ time: "", content: "" }],
      }, //目前操作的表單內容
      oldList: {}, //存放修改前資料
      emptyRules: [(v) => !!v || "不可空白"],
      title: "", //抬頭
      titleClass: "", //抬頭顯示樣式
      process: "", //儲存執行動作
      // memberArray:[],  //參加人員
      uitems: [], //所有病歷名稱
      selectedFile: "", //選擇的上傳檔案資料
      fd: new FormData(), //上傳的檔案及文案內容
      picItems: [], //活動圖片暫存
      base_url: this.$store.state.base_url,

      picItemsChanged: false, //用來判斷是否有改變圖片-> 有的話就一定要執行存檔或修改
    };
  },

  computed: {
    userCounts() {
      //參加人數
      var man = this.list.memberArray.filter((item) => {
        return item.sex == "男";
      });
      var women = this.list.memberArray.filter((item) => {
        return item.sex == "女";
      });
      return (
        "總人數:" +
        this.list.memberArray.length +
        "人, 男:" +
        man.length +
        "人, 女:" +
        women.length +
        "人"
      );
    },
  },

  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("childFn", function (item) {
      this.dialog = true;
      this.enterEdit(item);
    });

    this.getUserData();
  },

  methods: {
    //2023.5.20 加入一定要透過這個才能關閉視窗的功能 由於圖片數量可能有變化,所以如果圖片數量跟原本不一樣時,那就一定要存檔才行
    async checkClose() {
      let pass = false;
      //新增狀態 圖片數量為0就可以關閉,不為0時就要警告要刪掉所有圖片才行
      if (this.process == "add") {
        if (this.list.picItems && this.list.picItems.length) {
          let yn = await this.$confirm(
            "取消新增將刪除所有已上傳圖片!!確認刪除?"
          ).then((res) => {
            if (res) {
              let url =
                "general/delPicMulti/" +
                this.$store.state.databaseName +
                "/activity/";
              let postPicName = {
                picNames: this.list.picItems,
              };
              this.$api.options(url, postPicName);
              this.dialog = false;
            }
          });

          if (!yn) {
            return false;
          }
        }else{
          this.dialog = false;
        }
      }

      if (this.process == "edit") {
        if (this.picItemsChanged){
          let pop = {
            msg: "圖片內容有修改,請按修改後存檔!!",
            type: true,
            theme: "warning",
          };
          this.$store.commit("snackbar", pop);
        }else{
          this.dialog = false;
        }
      }
    },
    enterAdd() {
      //執行新增流程
      this.process = "add";
      this.title = "新增活動參與紀錄表";
      this.titleClass = "font-weight-black error lighten-2";
      this.list = {
        memberArray: [],
        scheduleArray: [{ time: "", content: "" }],
      };
    },
    enterEdit(item) {
      //執行修改流程
      this.process = "edit";
      this.title = "修改活動參與紀錄表";
      this.titleClass = "font-weight-black success lighten-2";
      this.list = JSON.parse(JSON.stringify(item)); //列表中複製一份顯示及修改用資料

      //取得活動圖片資訊
      // this.getPic();
    },
    addOK() {
      //確認新增
      if (this.$refs.form.validate()) {
        this.$emit("finishAdd", this.list);
        this.dialog = false;
      } else {
        var pop = { msg: "活動名稱不可為空白!!", type: true, theme: "warning" };
        this.$store.commit("snackbar", pop);
      }
    },
    editOK() {
      //確認修改
      if (this.$refs.form.validate()) {
        this.$emit("finishEdit", this.list);
        this.dialog = false;
        this.picItemsChanged = false;
      } else {
        var pop = { msg: "活動名稱不可為空白!!", type: true, theme: "warning" };
        this.$store.commit("snackbar", pop);
      }
    },
    getUserData() {
      //取得所有資料
      this.$api.options("user/getAll/" + this.$store.state.databaseName).then(
        (rs) => {
          this.uitems = rs
            .filter((i) => !!i.bed_name)
            .sort((a, b) => (a.bed_name > b.bed_name ? 1 : -1))
            .map((item) => {
              return {
                snkey: item.snkey,
                text: `${item.name}`,
                sex: item.sex,
              };
            });
        }
      );
    },
    activityAdd() {
      //新增活動時間表
      this.list.scheduleArray.push({ time: "", content: "" });
    },
    activityDel(index) {
      //刪除活動時間表
      this.list.scheduleArray.splice(index, 1);
    },
    onFileSelected(event) {
      // console.log(event.target.files)
      //處理多筆上傳的資料
      this.selectedFile = event.target.files;
      var errorMsg = ""; //判斷檔案是否為圖檔及檔案大小

      this.selectedFile.forEach((item) => {
        if (item.name.includes("++")) {
          errorMsg = "檔案:" + item.name + "檔名不可包含( ++ )";
        }
        if (item.size > 1024 * 1024 * 5) {
          //檔案大小設定-最大5MB
          errorMsg = "檔案:" + item.name + "不能超過5M唷";
        }

        if (errorMsg != "") {
          var pop = { msg: errorMsg, type: true, theme: "warning" };
          this.$store.commit("snackbar", pop);
        } else {
          this.fd.append("files[]", item);
        }
      });

      if (errorMsg == "") {
        this.fd.append("snkey", this.list.snkey);
        this.onUpload();
      }

      event.target.value = "";
    },
    onUpload() {
      //多筆上傳
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/fileUploadMulti/" +
            this.$store.state.databaseName +
            "/activity/activity",
          this.fd
        )
        // ,{
        //   onUploadProgress: uploadEvent =>{
        //     this.$store.commit("setLoadingPercent",Math.round(uploadEvent.loaded / uploadEvent.total *100))
        //   }
        // }
        // )
        .then((rs) => {
          // this.$refs.fileInput.value = "";
          this.picItemsChanged = true;

          if (!this.list.picItems) {
            this.$set(this.list, "picItems", []);
          }
          this.list.picItems = this.list.picItems.concat(rs.data);

          this.selectedFile = [];
          this.fd = new FormData(); //這裡要重置,不然會一直重新上傳
          // this.$store.commit("setLoadingOff"); //讀取畫面off
          // this.getPic();
        });
    },
    // getPic() {  //取得活動圖片
    //   //this.$store.commit("setLoadingOn"); //讀取畫面on
    //   this.axios
    //     .post("general/getPicBySnkey/" + this.$store.state.databaseName + "/activity/activity/" + this.list.snkey)
    //     .then(rs => {
    //       // console.log(rs.data);
    //       if (rs.data.state == 1){
    //         this.picItems = rs.data.picArray;
    //       }else{
    //         this.picItems = [];
    //       }
    //         // this.$store.commit("setLoadingOff"); //讀取畫面off
    //     });
    // },
    delPic(item, index) {
      //刪除圖片

      this.$confirm("確認刪除?").then((res) => {
        if (res) {
          this.picItemsChanged = true;

          //刪除資料
          this.axios
            .post(
              "general/delPic/" +
                this.$store.state.databaseName +
                "/activity/activity/" +
                item
            )
            .then((rs) => {
              this.list.picItems.splice(index, 1);
              var pop = { msg: "已刪除", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.picItems.splice(index, 1);
            });
        }
      });
    },
  },
};
</script>

