<template>
  <div class="activitylist">
    <v-row>
      <v-col cols="12" md="6">
        <h2 class="subheading grey--text">
          <v-icon class="mx-1">mdi-ticket</v-icon>
          <span>活動參與紀錄表</span>
        </h2>
      </v-col>
      <v-col cols="12" md="6">
        <!-- 功能表 -->
        <v-toolbar dense>
          <v-text-field
            hide-details
            prepend-icon="mdi-magnify"
            single-line
            label="搜尋"
            v-model="serachKey"
          ></v-text-field>
          <popupadd @finishAdd="finishAdd" @finishEdit="finishEdit" ref="enterEdit"></popupadd>
        </v-toolbar>
      </v-col>
    </v-row>

    <v-card>
      <v-card-title>
        <v-spacer></v-spacer>
      </v-card-title>

      <v-card-text>
        <v-card class="mt-2">
          <v-card-text>
            <!-- 表單內容 -->
            <v-data-iterator
              :items="searchfilter"
              :items-per-page.sync="itemsPerPage"
              :footer-props="{ itemsPerPageOptions }"
            >
              <template v-slot:default="props">
                <v-simple-table fixed-header class="mt-2 text-no-wrap">
                  <template v-slot:default>
                    <thead class="title">
                      <tr>
                        <th rowspan="3"></th>
                        <th rowspan="2">活動名稱</th>
                        <th rowspan="2">日期</th>
                        <th rowspan="2">時間</th>
                        <th rowspan="2">目標</th>
                        <th rowspan="2">地點</th>
                        <th rowspan="2">器具設備</th>
                        <th rowspan="2">帶領者</th>
                        <th rowspan="2">協助者</th>
                        <th rowspan="2">活動說明</th>
                        <th rowspan="2">活動檢討與建議</th>
                        <th rowspan="2">紀錄人</th>
                        <th rowspan="2">修改紀錄</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item,index) in props.items" :key="index">
                        <td>
                          <v-menu transition="scale-transition" offset-y>
                            <template v-slot:activator="{ on }">
                              <v-btn color="primary" dark v-on="on">功能</v-btn>
                            </template>
                            <v-list>
                              <v-list-item @click="enterEdit(item)">
                                <v-list-item-title>修改</v-list-item-title>
                              </v-list-item>
                              <v-list-item @click="enterDel(item)">
                                <v-list-item-title>刪除</v-list-item-title>
                              </v-list-item>
                              <v-list-item @click.stop="print(item)">
                                <v-list-item-title>列印</v-list-item-title>
                              </v-list-item>
                            </v-list>
                          </v-menu>
                        </td>
                        <td>{{item.name}}</td>
                        <td>{{item.date}}</td>
                        <td>{{item.time}}</td>
                        <td>{{item.target}}</td>
                        <td>{{item.location}}</td>
                        <td>{{item.device}}</td>
                        <td>{{item.leader}}</td>
                        <td>{{item.helper}}</td>
                        <td>{{item.explanation}}</td>
                        <td>{{item.suggest}}</td>
                        <td>{{item.create_man}}</td>
                        <td>{{item.edit_man}}</td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
              </template>
            </v-data-iterator>
          </v-card-text>
        </v-card>
      </v-card-text>
    </v-card>

    <!-- <pre>    {{items[0]}}    </pre> -->
  </div>
</template>

<script>
import popupadd from "@/views/main/ActivityAdd";
import dayjs from "dayjs";

export default {
  components: { popupadd },
  data() {
    return {
      items: [], //活動參與資料
      serachKey: "", //搜尋欄
      itemsPerPageOptions: [10, 20, 30],
      itemsPerPage: 10,
      temp: "",
    };
  },

  computed: {
    searchfilter() {
      var keys = this.serachKey.split(" ");
      var str = "";

      return keys.reduce(function (prev, element) {
        return prev.filter((item) => {
          str = JSON.stringify(item).toUpperCase();
          if (str.includes(element.toUpperCase())) {
            return item;
          }
        });
      }, this.items)
      .sort((a,b)=>a.date < b.date ? 1 : -1);

    },
  },

  mounted() {
    this.auth = this.$store.state.pData; //各畫面權限暫存
    this.getAllData();
  },

  methods: {
    getAllData() {
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      //取得所有資料
      this.$api.options("general/getAll/" + this.$store.state.databaseName + "/activity")
        .then((rs) => {
          this.items = rs.map(i=>{
            let newItem = JSON.parse(i.datalist);
            newItem.snkey = i.snkey;
            return newItem;
          });
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    sortBy() {
      //依排序
      this.items = this.items.sort(function (a, b) {
        return a.time < b.time ? 1 : -1;
      });
    },
    finishAdd(item) {
      //執行新增(在popupadd執行新增時,回傳後執行的動作,item為傳回的list表單內容)
      this.$store.commit("getTime"); //取得現在時間
      item["create_man"] =
        this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //執行動作的人,時間,內容

      let postData = {
        datalist : JSON.stringify(item)
      }

      this.$api.options("general/add/" + this.$store.state.databaseName + "/activity",postData)
        .then((rs) => {
          if (rs.state == 1) {
            var pop = { msg: "已新增", type: true, theme: "success" };
            this.$store.commit("snackbar", pop);
            this.getAllData();
          }
        });
    },
    //進入修改作業 -> 顯示修改頁面
    enterEdit(item) {
      this.$refs.enterEdit.$emit("childFn", item);
    },
    finishEdit(item) {
      //執行新增(在popupadd執行修改時,回傳後執行的動作,item為傳回的list表單內容)
      if (item.edit_man == undefined) { item.edit_man = ""; }
      item.edit_man = this.$store.state.pData.username + "(" + dayjs().format("YYYY-MM-DD HH:mm:ss") + ")" + item.edit_man; //執行動作的人,時間,內容

      let postData = {
        snkey: item.snkey,
        datalist: JSON.stringify(item),
        edit_man: "",  //伺服端要有這個不然不能改.^^
      };

      this.$api.options(
          "general/edit/" + this.$store.state.databaseName + "/activity",
          postData
        )
        .then((rs) => {
          if (rs.state == 1) {
            this.items.forEach((i,index)=>{
              if (item.snkey == i.snkey){
                this.items.splice(index,1,item)
              }
            })
            var pop = { msg: "已修改", type: true, theme: "success" };
            this.$store.commit("snackbar", pop);
          }
        });
    },
    enterDel(delData) {
      //刪除功能
      this.$confirm("確認刪除?將連同相關圖片一起刪除!!").then((res) => {
        if (res) {
          //刪除所有照片
          let url =
                "general/delPicMulti/" +
                this.$store.state.databaseName +
                "/activity/";
              let postPicName = {
                picNames: delData.picItems,
              };
              this.$api.options(url, postPicName);
          // this.axios
          //   .post(
          //     "general/delAllPic/" +
          //       this.$store.state.databaseName +
          //       "/activity/activity/" +
          //       delData.snkey
          //   )
          //   .then((rs) => {
          //     // console.log(rs.data)
          //   });

          delData.delman =
            this.$store.state.pData.username +
            "(" +
            dayjs().format("YYYY-MM-DD HH:mm:ss") +
            ")";
          var postData = {
            snkey: delData.snkey,
            tablename: "activity",
            info: JSON.stringify(delData),
          };

          this.$api.delete("activity",postData)
            .then((rs) => {
              console.log('delrs',rs);
              if (rs.state == 1) {
                var pop = { msg: "已刪除", type: true, theme: "success" };
                this.$store.commit("snackbar", pop);
                this.getAllData();
              }
            });
          
        }
      });
    },
    //列印功能
    print(item){
      let routeData = this.$router.resolve({path: "/print/activityprint",query:{data:JSON.stringify(item)}});
      window.open(routeData.href, "_blank");
    },
  },
};
</script>


<style lang="scss">
.activitylist {
  h2 {
    span {
      vertical-align: middle;
    }
  }
  thead {
    th {
      font-size: 1.2rem;
      font-family: "微軟正黑體";
    }
  }
  tbody {
    td {
      font-size: 1rem;
    }
  }
}
</style>